import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { UserService } from '../../../../api/services/user.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { elementEventFullName } from '@angular/compiler/src/view_compiler/view_compiler';

@Component({
  selector: 'app-helpdesk-setup',
  templateUrl: './helpdesk-setup.component.html',
  styleUrls: ['./helpdesk-setup.component.scss']
})
export class HelpdeskSetupComponent implements OnInit {
  
  categoryPrivateData:string = "";
  categoryPrivateListData:any;

  categoryCommonData:string = "";
  categoryCommonListData:any;

  isCategoryDataLoaded:boolean = false;

  staffTypeData:any;

  modalService:any;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isStaffLoaded:boolean = false;

  staffType:string = "";
  staffTypeId:number;
  staffCategory:string = "";

  staffDeleteTypeId:string = ""

  errorMessage:string = "";
  isError:boolean = false; 
  selectedInput:string = "";

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private userService:UserService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }
  openDialog(data): void {
    this.modalService.showTicketEditModal(data);
  }
  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  submitStaffForm(form) {
    
    this.isError = false;

    //common category
    if(this.staffType == "26"){

      this.staffTypeId = 17;

      var categoryAvailable = _.some( this.categoryCommonListData, item => {
        return item.lookupValueName === this.staffCategory;
      });

      if(categoryAvailable) {
        this.isError = true;
        this.errorMessage = 'Category already available';
        this.isStaffLoaded = true;
      }
      else {

        this.isStaffLoaded = false;

        var details = {
  
          "lookupTypeId": this.staffTypeId,
          "lookupValueName": this.staffCategory,
          "description": this.staffCategory,
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": "2019-11-16T14:50:08.217Z",
          "updatedBy": 0,
          "updatedOn": "2019-11-16T14:50:08.217Z"

        }


        this.lookupService.addLookupValue(details).subscribe((res:any) => {


              this.isStaffLoaded = true;
              this.isCategoryDataLoaded = false;

              this.lookupService.getLookupValueByLookupTypeId(this.staffTypeId).subscribe((res:any) => {

                 var categoryCommonListData = res.filter(item => {
                  return item.isActive;
                 });

                 this.categoryCommonListData = categoryCommonListData;
                 this.isCategoryDataLoaded = true;


              },
              error => {

              }
              );

        },
        error => {

        }
        ); 


      }
      

    }
    else {

      this.staffTypeId = 17;

      var categoryAvailable = _.some( this.categoryPrivateListData, item => {
        return item.lookupValueName === this.staffCategory;
      });

      if(categoryAvailable) {
        this.isError = true;
        this.errorMessage = 'Category already available';
        this.isStaffLoaded = true;
      }

      else {

          this.isStaffLoaded = false;

          var details = {
      
            "lookupTypeId": this.staffTypeId,
            "lookupValueName": this.staffCategory,
            "description": this.staffCategory,
            "isActive": true,
            "insertedBy": parseInt(this.cookieService.get('userId')),
            "insertedOn": "2019-11-16T14:50:08.217Z",
            "updatedBy": 0,
            "updatedOn": "2019-11-16T14:50:08.217Z"
          }

          this.lookupService.addLookupValue(details).subscribe((res:any) => {


              this.isStaffLoaded = true;
              this.isCategoryDataLoaded = false;


              this.lookupService.getLookupValueByLookupTypeId(this.staffTypeId).subscribe((res:any) => {

                 var categoryPrivateListData = res.filter(item => {
                   return item.isActive;
                 });

                 this.categoryPrivateListData = categoryPrivateListData;
                 this.isCategoryDataLoaded = true;


              },
              error => {

              }
              );

          },
          error => {

          }
          ); 

        }

    }



  }
  selectColInput(type){
    this.selectedInput = type;
  }
  ngOnInit() {


    //get all staff type
      this.lookupService.getLookupValueByLookupTypeId(7).subscribe((res:any) => {

        this.staffTypeData = res;

        this.isStaffLoaded = true;


      },
      error => {

      }
      );

  	//get all private category
    this.lookupService.getLookupValueByLookupTypeId(16).subscribe((res:any) => {

      let categoryPrivateListData = res.filter((item:Object) => 
      item['isActive'] ===true
       );

      categoryPrivateListData.forEach(element => {       
        element.lookupValueName === "Electrical" ? (element.supervisor_name = "David", element.level1 = "john",element.level2 = "daniel",element.l1escdays =8 ,element.l2escdays = 5 ) :
        element.lookupValueName === "Painting" ? element.supervisor_name = "John"  :
        element.lookupValueName === "Refrigerator" ? element.supervisor_name = "Sam"  : ''
       });
       console.log(categoryPrivateListData)
      this.categoryPrivateListData = categoryPrivateListData;

      //get all common category
      this.lookupService.getLookupValueByLookupTypeId(17).subscribe((res:any) => {

        var categoryCommonListData = res.filter(item => {
          return item.isActive;
        });

        this.categoryCommonListData = categoryCommonListData;

        this.isCategoryDataLoaded = true;


      },
      error => {

      }
      );


    },
    error => {
      
    }
    );


    // delete lookupvalue
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        if(this.staffDeleteTypeId == "27"){

          var params = {
            lookupValueId: this.categoryPrivateListData[index].id,
            updateUserId: parseInt(this.cookieService.get('userId'))
          }

        }
        else {

          var params = {
            lookupValueId: this.categoryCommonListData[index].id,
            updateUserId: parseInt(this.cookieService.get('userId'))
          }

        }
        

        this.isCategoryDataLoaded = false;

        this.userService.deleteUserById(params).subscribe((res:any) => {

          if(this.staffDeleteTypeId == "26"){
            this.categoryPrivateListData.splice(index, 1);
          }
          else {
            this.categoryCommonListData.splice(index, 1);
          }
          this.isCategoryDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    } )

  }

}
